	module.exports = {"data":{"markdownRemark":{"html":"<h2>Bubble Sort</h2>\n<p>Bubble Sort is the simplest sorting algorithm that works by repeatedly swapping the adjacent elements if they are in wrong order. Bubble sort has a worst case runtime of O(n^2) since there are (n choose 2) = O(n^2) different ways of choosing a starting and ending element; this case only occurs if the input array is sorted in reverse order. The best case runtime is O(n) since it makes no swaps when the input list is already sorted.</p>\n<h3>Example:</h3>\n<h4>First Pass:</h4>\n<p>( 5 1 4 2 8 ) –> ( 1 5 4 2 8 ), Here, algorithm compares the first two elements, and swaps since 5 > 1.</p>\n<p>( 1 5 4 2 8 ) –> ( 1 4 5 2 8 ), Swap since 5 > 4</p>\n<p>( 1 4 5 2 8 ) –> ( 1 4 2 5 8 ), Swap since 5 > 2</p>\n<p>( 1 4 2 5 8 ) –> ( 1 4 2 5 8 ), Now, since these elements are already in order (8 > 5), algorithm does not swap them.</p>\n<h4>Second Pass:</h4>\n<p>( 1 4 2 5 8 ) –> ( 1 4 2 5 8 )</p>\n<p>( 1 4 2 5 8 ) –> ( 1 2 4 5 8 ), Swap since 4 > 2</p>\n<p>( 1 2 4 5 8 ) –> ( 1 2 4 5 8 )</p>\n<p>( 1 2 4 5 8 ) –> ( 1 2 4 5 8 )</p>\n<p>Now, the array is already sorted, but our algorithm does not know if it is completed. The algorithm needs one whole pass without any swap to know it is sorted.</p>\n<h4>Third Pass:</h4>\n<p>( 1 2 4 5 8 ) –> ( 1 2 4 5 8 )</p>\n<p>( 1 2 4 5 8 ) –> ( 1 2 4 5 8 )</p>\n<p>( 1 2 4 5 8 ) –> ( 1 2 4 5 8 )</p>\n<p>( 1 2 4 5 8 ) –> ( 1 2 4 5 8 )</p>\n<h4>Properties</h4>\n<ul>\n<li>Space complexity: O(1)</li>\n<li>Best case performance: O(n)</li>\n<li>Average case performance: O(n*n)</li>\n<li>Worst case performance: O(n*n)</li>\n<li>Stable: Yes</li>\n</ul>\n<h3>Video Explanation</h3>\n<p><a href=\"https://www.youtube.com/watch?v=Jdtq5uKz-w4\">Bubble sort in easy way</a></p>\n<p>This code will use bubble sort to sort the array.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-js\"><code><span class=\"token keyword\">let</span> arr <span class=\"token operator\">=</span> <span class=\"token punctuation\">[</span><span class=\"token number\">1</span><span class=\"token punctuation\">,</span> <span class=\"token number\">4</span><span class=\"token punctuation\">,</span> <span class=\"token number\">7</span><span class=\"token punctuation\">,</span> <span class=\"token number\">45</span><span class=\"token punctuation\">,</span> <span class=\"token number\">7</span><span class=\"token punctuation\">,</span><span class=\"token number\">43</span><span class=\"token punctuation\">,</span> <span class=\"token number\">44</span><span class=\"token punctuation\">,</span> <span class=\"token number\">25</span><span class=\"token punctuation\">,</span> <span class=\"token number\">6</span><span class=\"token punctuation\">,</span> <span class=\"token number\">4</span><span class=\"token punctuation\">,</span> <span class=\"token number\">6</span><span class=\"token punctuation\">,</span> <span class=\"token number\">9</span><span class=\"token punctuation\">]</span><span class=\"token punctuation\">;</span>\n<span class=\"token keyword\">let</span> sorted <span class=\"token operator\">=</span> <span class=\"token boolean\">false</span>\n\n<span class=\"token keyword\">while</span><span class=\"token punctuation\">(</span><span class=\"token operator\">!</span>sorted<span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n  sorted <span class=\"token operator\">=</span> <span class=\"token boolean\">true</span>\n  <span class=\"token keyword\">for</span><span class=\"token punctuation\">(</span><span class=\"token keyword\">var</span> i<span class=\"token operator\">=</span><span class=\"token number\">0</span><span class=\"token punctuation\">;</span> i <span class=\"token operator\">&lt;</span> arr<span class=\"token punctuation\">.</span>length<span class=\"token punctuation\">;</span> i<span class=\"token operator\">++</span><span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n    <span class=\"token keyword\">if</span><span class=\"token punctuation\">(</span>arr<span class=\"token punctuation\">[</span>i<span class=\"token punctuation\">]</span> <span class=\"token operator\">&lt;</span> arr<span class=\"token punctuation\">[</span>i<span class=\"token number\">-1</span><span class=\"token punctuation\">]</span><span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n      <span class=\"token keyword\">let</span> temp <span class=\"token operator\">=</span> arr<span class=\"token punctuation\">[</span>i<span class=\"token punctuation\">]</span><span class=\"token punctuation\">;</span>\n      arr<span class=\"token punctuation\">[</span>i<span class=\"token punctuation\">]</span> <span class=\"token operator\">=</span> arr<span class=\"token punctuation\">[</span>i<span class=\"token number\">-1</span><span class=\"token punctuation\">]</span><span class=\"token punctuation\">;</span>\n      arr<span class=\"token punctuation\">[</span>i<span class=\"token number\">-1</span><span class=\"token punctuation\">]</span> <span class=\"token operator\">=</span> temp<span class=\"token punctuation\">;</span>\n      sorted <span class=\"token operator\">=</span> <span class=\"token boolean\">false</span><span class=\"token punctuation\">;</span>\n    <span class=\"token punctuation\">}</span>\n  <span class=\"token punctuation\">}</span>\n<span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<h3>More Information</h3>\n<ul>\n<li><a href=\"https://en.wikipedia.org/wiki/Bubble_sort\">Wikipedia</a></li>\n<li><a href=\"https://www.youtube.com/watch?v=Jdtq5uKz-w4\">Bubble Sort Algorithm - MyCodeSchool (video)</a></li>\n</ul>","fields":{"slug":"/algorithms/sorting-algorithms/bubble-sort/"},"frontmatter":{"title":"Bubble Sort"}}},"pathContext":{"slug":"/algorithms/sorting-algorithms/bubble-sort/"}}

